/**
 * Funo para obter descrio traduzida baseada no idioma
 */
export function getBreedDescription(breedName, originalDescription, language) {
  if (language === 'pt-BR') {
    return breedDescriptionsPtBr[breedName] || originalDescription;
  }
  return originalDescription; // Retorna ingls original
}
